/* Login component-specific styles */
.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.login_box {
  background-color: #fff;
  border: 1px solid #ccc;
  padding: 20px;
  width: 300px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.login-header {
  text-align: center;
  font-size: 1.5em;
  margin-bottom: 20px;
}

.input_box {
  margin-bottom: 15px;
}

.input-field {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.label {
  display: block;
  margin-bottom: 5px;
  font-size: 0.9em;
}

.icon {
  position: absolute;
  margin-left: -25px;
  margin-top: 12px;
  font-size: 1.2em;
  color: #888;
}

.input-submit {
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1em;
}

.input-submit:hover {
  background-color: #0056b3;
}

/* Sign-up link */
.input_box a {
  text-decoration: none;
  color: #007bff;
  font-size: 0.9em;
}

.input_box a:hover {
  text-decoration: underline;
  color: #0056b3;
}
